.frame {
  display: flex;
  background: linear-gradient(90deg, #000 50%, #FAF3E0 50%);
  border: 2px solid #fff;
  padding: 20px;
  color: #fff;
  font-size: 1.2em;
  max-width: 600px;
  width: 100%;
}

.text-container {
  flex: 1;
  padding: 20px;
}

.image-container {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #FAF3E0;
  border-left: 2px solid #000;
  padding: 20px;
}

.image {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
}